<template>
  <div class="row center-horizontal slide-down phone-width">
    <div class="row form-box box-border">
      <div class="row">
        <h2>add players</h2>
      </div>
      <hr />
      <div class="row">
        <div style="text-align: left">
          <div>
            <h4>name:</h4>
          </div>
          <div style="display: grid; grid-template-columns: 80% auto; ">
            <input
              v-model="inputName"
              class="box-border"
              type="text"
              maxlength="12"
              @keyup.enter="addPlayer"
            />
            <button
              class="button-green box-border add-button"
              @click="addPlayer"
            >
              <h4>add</h4>
            </button>
          </div>
        </div>
      </div>
      <div class="row player-list-box overflow-auto">
        <div
          v-for="player in players"
          :key="player.id"
          class="player-box box-border "
        >
          <div class="center-vertical">
            <h4>{{ player.name }}</h4>
          </div>
          <div class="center-vertical">
            <button
              class="close-button box-border"
              @click="removePlayer(player.id)"
            >
              <i class="fas fa-times"></i>
            </button>
          </div>
        </div>
      </div>
    </div>
    <div class="row center-horizontal pick-categories-box">
      <button
        v-if="this.players.length >= 2"
        class="button-green box-border"
        @click="pickCategories"
      >
        <h3>set game rules</h3>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return { inputName: "" };
  },
  methods: {
    addPlayer() {
      if (this.inputName == "") return;
      this.$store.dispatch("addPlayer", this.inputName);
      this.inputName = "";
    },
    removePlayer(playerId) {
      this.$store.dispatch("removePlayer", playerId);
    },
    pickCategories() {
      if (this.players.length < 2) return;
      this.$emit("pick-categories");
    },
  },
  computed: {
    players() {
      return this.$store.getters.players;
    },
  },
};
</script>

<style scoped>
.pick-categories-box {
  margin-top: 20px;
}
.close-button {
  width: 30px;
  height: 30px;
  background-color: var(--red);
}
.player-list-box {
  max-height: 450px;
  margin-top: 20px;
}
.form-box {
  background-color: var(--blue);
  padding: 20px;
  width: 100%;
}
.title-box {
  margin-bottom: 50px;
  background-color: var(--green);
}
.add-button {
  width: 50px;
  margin-left: 5px;
}
input {
  font-size: clamp(14px, 3vw, 26px);
}
.player-box {
  padding: 15px;
  display: grid;
  grid-template-columns: auto auto;
  justify-content: space-between;
  height: 70px;
  margin-bottom: 5px;
  background-color: var(--orange);
}
</style>
