<template>
  <div class="card-box box-border slide-up">
    <div class="card-box-inner">
      <div class="row">
        <span class="col" style="text-align: left;">
          <i :class="card.gameMode.icon"></i>
        </span>
        <h3 class="col-6">{{ card.gameMode.name }}</h3>
        <div class="col" style="text-align: right;">
          <h5>{{ cardsPlayed }}/{{ totalCards }}</h5>
        </div>
      </div>
      <hr style="padding: 0 !important; margin: 0 !important" />
      <div class="center-horizontal" style="margin-top: 60px">
        <div>
          <div class="card-grid">
            <div>
              <p class="small-text">
                {{
                  card.description + (card.hasRandomLetter ? randomLetter : "")
                }}
              </p>
              <div :class="card.points <= 20 ? 'easy-mode' : 'hard-mode'">
                <p class="x-small-text ">points: {{ card.points }}</p>
              </div>
            </div>
            <div class="buttons-box">
              <div v-if="card.gameMode == gameModes.Drinking">
                <button class="button-green box-border" @click="onDoItClick">
                  <h3>done</h3>
                </button>
              </div>
              <div v-else>
                <div>
                  <button
                    class="button-green box-border button-text"
                    style="margin-bottom: 15px;"
                    @click="onDoItClick"
                  >
                    <h3>
                      done
                    </h3>
                  </button>
                </div>
                <div style="">
                  <p
                    class="small-text"
                    style="display: inline-block; margin-right: 5px;"
                  >
                    or
                  </p>
                  <button class="button-red box-border" @click="onDontDoClick">
                    <h3>take {{ card.sips }} sips</h3>
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import GameModes from "@/classes/GameModes.js";

export default {
  data() {
    return {
      timer: 0,
      gameModes: GameModes,
      randomLetter: "",
      countDownStarted: false,
    };
  },
  props: {
    card: {},
    cardsPlayed: Number,
    totalCards: Number,
  },
  methods: {
    onDoItClick() {
      this.$emit("card-done", this.card.points);
      this.countDownStarted = false;
    },
    onDontDoClick() {
      this.$emit("card-done", 0);
      this.countDownStarted = false;
    },
  }
};
</script>

<style scoped>
@media (max-width: 768px) {
  .card-grid {
    grid-template-rows: 120px 80px !important;
  }
}
.card-grid {
  display: grid;
  grid-template-rows: 200px 100px;
}

button {
  padding-top: 5px;
  padding-left: 25px;
  padding-right: 25px;
}
.card-box-inner {
  padding: 10px;
}
.card-box {
  background-color: var(--blue);
  height: 100%;
  width: 100%;
}
</style>
