<template>
  <div>
    <div class="center-horizontal">
      <PickPlayers v-if="pickPlayers" v-on:pick-categories="goToGameRules" />
      <SetGameRules v-else v-on:on-start-game="startGame"/>
    </div>
  </div>
</template>

<script>
import PickPlayers from "@/components/PickPlayers.vue";
import SetGameRules from "@/components/SetGameRules.vue";

export default {
  data() {
    return { pickPlayers: true };
  },
  methods: {
    startGame() {
      this.$emit("on-start-game");
    },
    goToGameRules(){
      this.pickPlayers = false;
    }
  },
  components: {
    PickPlayers,
    SetGameRules,
  },
  mounted() {
    this.pickPlayers = true;
  }
};
</script>

<style scoped>
.categories-button {
  width: 400px;
}
.play-button-box {
  margin-top: 20px;
}
</style>