<template>
  <div class="row center-horizontal slide-down phone-width">
    <div class="row form-box box-border">
      <div class="row">
        <h2>game modes</h2>
      </div>
      <hr />
      <div
        v-for="mode in gameModes"
        :key="mode.name"
        class="center-vertical checkbox-box"
      >
        <div class="col-sm-auto  center-vertical" style="width: 40px">
          <i :class="mode.icon"></i>
        </div>
        <div class="col-sm-auto  center-vertical">
          <input
            class="form-check-input"
            type="checkbox"
            :value="mode"
            :disabled="
              mode == gameModes.Drinking || mode == gameModes.Challenges
            "
            v-model="checkedModes"
          />
        </div>
        <div class="col-md-auto checkbox-text">
          <h4>{{ mode.name }}</h4>
        </div>
      </div>
      <div class="row rounds-box">
        <h2>amount of rounds</h2>
      </div>
      <hr />
      <div class="row center-horizontal">
        <div class="col-2">
          <input
            class="form-check-input"
            type="radio"
            :value="twenty"
            v-model="rounds"
          />
          <h4 class="form-check-label" for="flexRadioDefault1">20</h4>
        </div>
        <div class="col-2">
          <input
            class="form-check-input"
            type="radio"
            :value="sixty"
            v-model="rounds"
          />
          <h4 class="form-check-label" for="flexRadioDefault1">60</h4>
        </div>
        <div class="col-2">
          <input
            class="form-check-input"
            type="radio"
            :value="hundred"
            v-model="rounds"
          />
          <h4 class="form-check-label" for="flexRadioDefault1">100</h4>
        </div>
        <div class="col-2">
          <input
            class="form-check-input"
            type="radio"
            :value="twohundred"
            v-model="rounds"
          />
          <h4 class="form-check-label" for="flexRadioDefault1">200</h4>
        </div>
      </div>
    </div>
    <div class="row center-horizontal start-game-box">
      <button class="button-green box-border" @click="startGame">
        <h3>start game</h3>
      </button>
    </div>
  </div>
</template>

<script>
import GameModes from "@/classes/GameModes.js";

export default {
  data() {
    return {
      twenty: 20,
      sixty: 60,
      hundred: 100,
      twohundred: 200,
      gameModes: GameModes,
      checkedModes: [GameModes.Drinking, GameModes.Challenges],
      rounds: 20,
    };
  },
  methods: {
    startGame() {
      this.$store.dispatch("setGameModes", this.checkedModes);
      this.$store.dispatch("setRounds", this.rounds);
      this.$emit("on-start-game");
    },
  },
};
</script>

<style scoped>
.checkbox-box {
  padding-left: 0px;
}
.checkbox-text {
  margin-left: 5px;
  padding-top: 10px;
}
.game-modes-box {
  margin-top: 10px;
  text-align: left;
}
.rounds-box {
  margin-top: 10px;
}
.start-game-box {
  margin-top: 20px;
}
i {
  margin-right: 2px;
}
input {
  border: solid #2c3e50 2px !important;
  border-radius: 10px !important;
  width: 30px !important;
  height: 30px !important;
}
.form-box {
  background-color: var(--blue);
  padding: 20px;
  width: 100%;
}
</style>
