<template>
  <div>
    <div v-if="!roundOver" class="row center-horizontal">
      <div class="col-md-auto center-horizontal">
        <div class="game-box">
          <div
            class="center-horizontal"
            style="align-items: flex-end; margin-bottom: 20px"
          >
            <Player
              :key="players[activePlayerIndex].id"
              :name="players[activePlayerIndex].name"
              :points="players[activePlayerIndex].points"
              class="slide-up"
            />
          </div>
          <div class="center-horizontal">
            <Card
              v-on:card-done="goNextCard"
              :card="cards[activeCardIndex]"
              :cardsPlayed="cardsPlayed"
              :totalCards="totalRounds"
            />
          </div>
        </div>
      </div>

      <div class="col-md-auto box-border slide-down highscore-box">
        <div class="row">
          <div class="center-horizontal" style="padding: 20px">
            <i class="fas fa-medal fa-3x"></i>
            <h2>leaderboard</h2>
          </div>
        </div>
        <div class="overflow-auto" style="padding: 5px">
          <div class="row">
            <Player
              v-for="player in displayPlayers"
              :key="player.id"
              :name="player.name"
              :points="player.points"
              style="margin-bottom: 5px"
              class="slide-down"
            />
          </div>
        </div>
      </div>
    </div>
    <RoundOver
      v-else
      v-on:on-play-again="restartRound"
      v-on:on-go-main-menu="goMainMenu"
      :players="winningPlayers"
    />
  </div>
</template>

<script>
import Player from "@/components/Player.vue";
import Card from "@/components/Card.vue";
import RoundOver from "@/components/RoundOver.vue";

export default {
  data() {
    let activePlayerIndex = 0;
    let activeCardIndex = 0;
    let cardsPlayed = 0;
    let roundOver = false;
    let winningPlayers;

    return {
      activePlayerIndex,
      activeCardIndex,
      cardsPlayed,
      roundOver,
      winningPlayers,
    };
  },
  methods: {
    goNextCard(points) {
      this.updatePlayerPoints(points);

      if (this.activeCardIndex == this.cards.length - 1) {
        this.resetCards();
      } else {
        this.activeCardIndex++;
      }

      this.cardsPlayed++;
      if (this.totalRounds < this.cardsPlayed) {
        this.endRound();
        return;
      }

      this.activePlayerIndex =
        (this.activePlayerIndex + 1) % this.players.length;
    },
    updatePlayerPoints(points) {
      this.players[this.activePlayerIndex].points += points;
      this.$store.dispatch(
        "updatePlayerPoints",
        this.players[this.activePlayerIndex]
      );
    },
    restartRound() {
      this.$store.dispatch("resetPlayersPoints");
      this.resetCards();
      this.cardsPlayed = 0;
    },
    goMainMenu() {
      this.$emit("go-main-menu");
    },
    resetCards() {
      this.shuffleCards();
      this.activeCardIndex = 0;
      this.roundOver = false;
    },
    endRound() {
      let highestPoints = this.displayPlayers[0].points;
      this.winningPlayers = this.displayPlayers.filter(
        (p) => p.points == highestPoints
      );
      this.roundOver = true;
    },
    compare(a, b) {
      if (a.points > b.points) {
        return -1;
      }
      if (a.points < b.points) {
        return 1;
      }
      return 0;
    },
    shuffleCards() {
      this.$store.dispatch("shuffle");
    },
  },
  computed: {
    players() {
      return this.$store.getters.players;
    },
    cards() {
      let gameModes = this.$store.getters.gameModes;
      return this.$store.getters.cards.filter((c) =>
        gameModes.includes(c.gameMode)
      );
    },
    displayPlayers() {
      let players = [...this.$store.getters.players].sort(this.compare);
      return players;
    },
    totalRounds() {
      return this.$store.getters.rounds;
    },
  },
  mounted() {
    this.restartRound();
  },
  components: {
    Player,
    Card,
    RoundOver,
  },
};
</script>

<style scoped>
@media (max-width: 768px) {
  .game-box {
    grid-template-rows: 50px 400px !important;
    margin-bottom: 30px;
    width: 80vw !important;
    max-width: 350px;
  }
  .highscore-box {
    max-width: 270px;
    width: 60vw !important;
    height: auto !important;
  }
}
.game-box {
  display: grid;
  grid-template-rows: 200px 500px;
  width: 350px;
}
.highscore-box {
  padding: 20px;
  width: 270px;
  height: 700px;
  background-color: var(--blue);
  margin-bottom: 30px;
}
.row {
  padding-left: 0px !important;
  padding-right: 0px !important;
  margin-top: 0px !important;
  margin-left: 0px !important;
  margin-right: 0px !important;
}
</style>
