import Vue from 'vue'
import Vuex from 'vuex'
import Player from '@/classes/Player';

Vue.use(Vuex)

const state = {
  players: [],
  gameModes: [],
  rounds: 20
}

const mutations = {
  ADD_PLAYER(state, player){
    state.players.push(player);
  },
  REMOVE_PLAYER(state, playerId){
    state.players = state.players.filter(p => p.id != playerId);
  },
  UPDATE_PLAYER_POINTS(state, player){
   let index = state.players.findIndex(p => p.id == player.id);
   state.players[index].points = player.points;
  },
  RESET_PLAYERS_POINTS(state){
    state.players.forEach(p => {
      p.points = 0;
    });
  },
  SET_GAMEMODES(state, modes){
    state.gameModes = modes;
  },
  REMOVE_GAMEMODE(state, mode){
    state.gameModes = state.gameModes.filter(m => m != mode);
  },
  SET_ROUNDS(state, rounds){
    state.rounds = rounds;
  }
}

const actions = {
  addPlayer({commit}, name){
    let player = new Player(name);
        commit("ADD_PLAYER", player);
  },
  removePlayer({commit}, playerId){
    commit("REMOVE_PLAYER", playerId);
  },
  updatePlayerPoints({commit}, player){
    commit("UPDATE_PLAYER_POINTS", player);
  },
  resetPlayersPoints({commit}){
    commit('RESET_PLAYERS_POINTS');
  },
  setGameModes({commit}, modes){
    commit('SET_GAMEMODES', modes);
  },
  removeGameMode({commit}, mode){
    commit('REMOVE_GAMEMODE', mode);
  },
  setRounds({commit}, rounds){
    commit('SET_ROUNDS', rounds)
  }
}

const getters = {
  players(state){
    return state.players;
  },
  rounds(state){
    return state.rounds;
  },
  gameModes(state){
    return state.gameModes;
  }
}

import card from '@/store/card.js'

export const store = new Vuex.Store({
    state,
    mutations,
    actions,
    getters,
    modules: { card }
})