<template>
  <div class="center-horizontal center-vertical">
    <div class="round-over-box box-border">
      <div v-if="players.length == 1">
        <h2 style="margin-bottom: 10px">
          the winner is
        </h2>
        <div class="center-horizontal">
          <Player :name="players[0].name" :points="players[0].points" />
        </div>
      </div>
      <div v-else>
        <h2 style="margin-bottom: 5px">
          the winners are
        </h2>
        <div
          style="margin-top: 5px"
          class="center-horizontal"
          v-for="player in players"
          :key="player.id"
        >
          <Player :name="player.name" :points="player.points" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Player from "../components/Player.vue";
export default {
  props: {
    players: [],
  },
  methods: {
    startConfetti() {
      this.$confetti.start();
    },
    endConfetti() {
      this.$confetti.stop();
    },
    playAgain() {
      this.endConfetti();
      this.$emit("on-play-again");
    },
    goMainMenu() {
      this.endConfetti();
      this.$emit("on-go-main-menu");
    },
  },
  components: {
    Player,
  },
};
</script>

<style scoped>
.round-over-box {
  margin-bottom: 30px;
  padding: 40px;
  background-color: var(--blue);
}
.margin-top {
  margin-top: 20px;
}
</style>
