const mutations = {
    SHUFFLE(state){
        state.cards = shuffle([...state.cards]);
    }
}

const actions = {
    shuffle({commit}){
        commit("SHUFFLE");
    }
}

const getters = {
    cards(state){
        return state.cards;
    }
}

function shuffle(sourceArray) {
    for (var i = 0; i < sourceArray.length - 1; i++) {
        var j = i + Math.floor(Math.random() * (sourceArray.length - i));

        var temp = sourceArray[j];
        sourceArray[j] = sourceArray[i];
        sourceArray[i] = temp;
    }
    return sourceArray;
}

export default {
    state,
    actions,
    mutations,
    getters
}